    img {
        width: 350px;
        height: 110px; } }

#schedule {
    height: 150px;
    text-align: center;
    img {
        width: 350px;
        height: 110px; } }

#contacts {
    height: 150px;
    text-align: center;
    img {
        width: 350px;
